import React, {useState, useEffect, useCallback} from 'react'
import fetchJSON from './fetchJSON.js'
import {debounce} from './utils.js'
import SearchBarList from './SearchBarList'


const SearchBar = ({word,history,setSearchResult}) => {
  const [value,setValue] = useState(word)
  const [focused, setFocused] = useState(true)
  const [list,setList] = useState([])

  useEffect(() => {
    if (value.length>0){
      fetchDefinition(value)
    }
  }, [])
  const fetchCandidates = useCallback(debounce((value) => {
    if (value.length===0){return}
    fetchJSON({value}).then(setList)
  },200),[])

  const fetchDefinition = useCallback(debounce((value) => {
    setValue(value)
    setList([])
    fetchJSON({value,action:"define"}).then(d => {
      const pathname = "/" + value
      if (history.location.pathname !== pathname){
        history.push({pathname})
      }
      setSearchResult(d)
    })
  },100),[])

  const handleOnChange = ({target:{value}}) => {
    setValue(value)
    if (list.length === 0 || list[0].toLowerCase().indexOf(value.toLowerCase())!==0){
      fetchCandidates(value)
    }
    if (value.length === 0){ 
      setList([])
    }
  }
  const handleOnKeyDown = ({key}) => {
    if (key !== "Enter") { return }
    key = value
    if (list[0]){
      key = list[0]
    } else {
      //cancel previous debounced request
      fetchCandidates("")
    }
    fetchDefinition(key)
  }

  const handleOnFocus = b => setTimeout(() => setFocused(b),500) 

  return (<>
    <input type="text" placeholder="Search" value={value}
      onKeyDown={handleOnKeyDown} onChange={handleOnChange}
      onFocus={() => handleOnFocus(true)} onBlur={() => handleOnFocus(false)}/>
    <SearchBarList show={focused} items={list} onSelect={fetchDefinition} />
  </>)
}
export default SearchBar
